import React, { useState } from 'react'
import "./navbar.css"

import {GrCopy,GrMenu,GrClose} from "react-icons/gr"
import CV from "../../assert/Harsha-Vardhan-Resume.pdf"

const Navbar = () => {
   const [status,setStatus] = useState(false)
   const [activeNav,setActiveNav] = useState("#")
   const handleClick = () => {
      setStatus(!status)
   }
   console.log(status)
   return (
    <div id="nav-menu">
      <div id={!status?"nav":"hidden__nav"}>
         <div> Harsha </div>

         <div  id={!status?"menu":"hidden__menu"} >           
               <a className="nav-link home" href="/#"  onClick={() => setActiveNav("#")} id={activeNav === "#" ? "active":""} >Home</a>

               <a className="nav-link about" href="/#about" onClick={() => setActiveNav("#about")} id={activeNav === "#about" ? "active":""}> About Me</a>

               <a className="nav-link skills" href="/#skills" onClick={() => setActiveNav("#skills")} id={activeNav === "#skills" ? "active":""}>Skills</a>

               <a className="nav-link projects" href="/#projects" onClick={() => setActiveNav("#projects")} id={activeNav === "#projects" ? "active":""}>Projects</a>

               <a className="nav-link contact" href="/#contact" onClick={() => setActiveNav("#contact")} id={activeNav === "#contact" ? "active":""}>Contact</a>

          <button id="resume-button-1"  onClick={() =>window.open(CV)}   >
          <a  className="nav-link resume" id='resume-link-1'  download  href={CV}
                 
                 
                >Resume</a>
            </button> 
         </div>

         <div className='nav-hambarger' onClick={handleClick}>
            {!status?<GrMenu className='nav-icone' />:<GrClose className='nav-icone' />}
         </div>
         
      </div>
    </div>
  )
}

export default Navbar